export default {
  props: {
    label: String,
    value: String | Number,
    placeholder: {
      type: String,
      default: "",
    },
    type: {
      type: String,
      default: "text",
    },
    internal: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    uppercase: {
      type: Boolean,
      default: false,
    },
    options: {
      type: Object,
    },
    css: {
      type: Object
    },
    slim: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    isBounipunUrl(s) {
      if(s === undefined || s=== null || s === "")
        return false;
      return s.includes('bounipun.in')
    }
  },
};
